            "main_msg1": "位于青藏高原西北部的西藏阿里地区人烟稀少，却拥有独特的高原自然地貌，是众多山脉汇聚、多条河流发源的地区。军旅摄影师向文军，在阿里地区驻防20余年，曾多次飞行在阿里的上空。他数年累积的航拍图片，为我们开启了天上的阿里之行。",
            "main_msg2": "位于喜马拉雅山脉西段的边境小县普兰，因为被众多的雪山环绕被称为“雪围”。这里冰川资源非常丰富，从高空俯瞰景致壮观——挺立的角峰、柔美的刃脊、奔涌而下的冰舌和冰川末端的冰碛湖，构成一种气势磅磗的雄浑之美。这样的景观在喜马拉雅山脉有很多，景致不输名声在外的珠穆朗玛峰。",
            "main_msg3": "在中国版图上，新疆和西藏留给人们的印象迥异。沙漠、戈壁、绿洲、神奇诡谲的魔鬼城，这是人们心中的典型新疆；雪山、冰川、湖泊、倚山而建的红色佛教建筑，这是人们心中的典型西藏。如果你打开中国地形图，会发现有一条分界线，将这两个不同风格的地区分隔开来，这就是昆仑山和喀喇昆仑山。这两条山脉的北侧是我国面积最大的内陆盆地——塔里木盆地，南侧是世界屋脊青藏高原，从塔里木盆地进入青藏高原，首先到达的区域就是阿里地区。阿里位于青藏高原西北部，昆仑山、喀喇昆仑山、冈底斯山、喜马拉雅山由北向南构成阿里的骨架。这四条山脉有一个共同的特点，即由东向西，逐渐收缩，最后在西北角交会在一起，像是打了一个结。从来没有哪个地方可以像阿里一样，同时拥有数条重量级的山脉。在空中俯瞰，四条山脉汇聚下的阿里拥有多彩斑斓的地貌景观。从新疆喀什的机场起飞，沿着塔克拉玛干沙漠西侧飞行，舷窗下相继出现绿洲、沙漠和戈壁景观。在叶城与皮山之间，飞机向南沿一条不知名的河谷飞越昆仑山。机腹之下，出现昆仑山与塔里木盆地过渡带的红色碎屑岩形成的大面积丹霞和土林。在那之后，昆仑山壮观的雪山景观映入眼帘，一列列高耸的雪山扑面而来，连绵不绝。",
