            "main_msg2": "拉达克，是世界上最崎岖、最荒芜的山地之一。它的海拔在3000—7000米之间，大部分都是人烟稀少的高地，只有在海拔3000米左右的印度河上游附近、在喜马拉雅的冰川融水滋养出的小块绿洲上，拉达克的居民们世世代代繁衍生息，形成了一个个小小的村、镇。",
