import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../view/main.vue'
import Map from '../view/map.vue'
import Hot from '../view/hot.vue'
import Tsal from '../view/tsal.vue'
import Tanzi from '../view/tanzi.vue'
import About from '../view/about.vue'
import Zuozhe from '../view/zuozhe.vue'
Vue.use(VueRouter)

const routes = [
	// 路由重定向
    { path: '/', redirect: '/main' },
    { path: '/main',component: Main,meta: {title: '首页'}},
    { path: '/map',component: Map,meta: {title: '地图'}},
    { path: '/hot',component: Hot,meta: {title: '摄影大赛'}},
    { path: '/tanzi',component: Tanzi,meta: {title: '谈资'}},
    { path: '/about',component: About,meta: {title: '关于'}},
    { path: '/zuozhe',component: Zuozhe,meta: {title: '作者'}},
    { path: '/tsal/:tsal_id',component: Tsal, props:true, name: 'tsal', meta: {title: '天上阿里'}},
]

const router = new VueRouter({
  routes
})

export default router
